.typingSection{
	height: 40vh;
	margin-top:2rem;
}

.oneTyping{
	color: white;
	font-size: 4rem;
	font-family: 'Roboto Slab', serif;
}

.skillsetIcons{
	margin-top: 4rem;
} 

.skillseticon{
	height: 60px;
	margin: 0 1rem;
	transition: all .2s ease-in-out;
}

.skillseticon:hover{
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.arrowDownDiv{
	display:flex;
	justify-content:center;
	align-items:flex-end;
	height:7rem;
}

.arrowDown{
	color:white;
	font-size: 4rem;
	margin: 0 1rem;
	transition: all .2s ease-in-out;
	cursor: pointer;
}

.arrowDown:hover{
	color:#f52549;
	border: 3px solid #f52549;
    border-radius: 3rem;
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
}


@media only screen and (max-width: 650px) {
	.typingSection{
		height: 30vh;
	}
	.oneTyping{
		font-size: 2.5rem;
	}
	.skillseticon{
		height: 45px;
		margin: none;
	}
	.arrowDownDiv{
		height:10rem;
	}
} 
.footerIcons{
	color:white;
	font-size: 3rem;
	margin: 0 1rem;
	transition: all .2s ease-in-out;
}

.footerIcons:hover{
	color: #f52549;
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}
.App {
  width: 100%;
}

.barIcons{
	color:white;
	font-size: 3rem;
	margin: 0 1rem;
	transition: all .2s ease-in-out;
}

.barIcons:hover{
	color:#69e781;
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

@media only screen and (max-width: 650px) {
	.barIcons{
		font-size: 2.0rem;
	}
}
